<template>
    <div 
        class="alt-grid-container"
        :class="operatorClass"
        :style="containerStyle">
        <div 
            :style="getCardStyleForPlaceholder(placeholder)"
            class="alt-grid-item-drag-placeholder"
            :class="placeholderClass">
        </div>
        <div 
            ref="cards"
            v-for="(item, index) in layout"
            :key="item._id"
            :dg-id="item._id"
            :style="item.style"
            class="alt-grid-item"
            :class="[canDragClass(item.isDraggable), gridItemClass, item.gridItemClass]">
            <button 
                v-if="getFirstSetValue(item.isShowOriginCloseBtn, isShowOriginCloseBtn, true)"
                :class="[closeHandlerClass, item.closeHandlerClass]" 
                @click="closeWidget(item._id)"></button>
            <component :ref="item._id" :is="item.type" :injected-props="getPropsForInject(index, item)"></component>
            <span 
                v-if="getFirstSetValue(item.isResizable, isResizable, true)"
                class="alt-grid-item-resize-handler"
                :class="[resizeHandlerClass, item.resizeHandlerClass]"></span>
        </div>
        <div class="mask"></div>
    </div>
</template>

<script>
    // import elementResizeDetectorMaker from 'element-resize-detector'
    import {
        hasClass,
        findParentThoughEvtPath,
        getFirstSetValue,
        getVue,
        getVariType,
        getIndexOfArrayByAttr
    } from './utils/util';
    import watchBoxSize from './utils/watch-box-size.js'
    import Coordinate from './utils/coordinate'
    import coorTest from './utils/coordinate.test.js'
 
    import WidgetRender from './components/Widget.render.vue';
    import WidgetTemplate from './components/Widget.template.vue';
    import WidgetComponent from './components/Widget.vuecomponent.vue';

    import altStoreFactory from './store.js'

    let Vue = getVue();

    export default {
        name: 'app',
        altStore: altStoreFactory(),
        addWidgetType(){
            let args0 = arguments[0];
            let type = getVariType(args0);
            if(type === 'string'){
                this._addWidgetType(...arguments);
            } else if(type === 'object'){
                for(let key in args0){
                    args0.hasOwnProperty(key) && this._addWidgetType(key, args0[key]);
                }
            }
        },
        // 
        _addWidgetType(type, widget){
            let parentWidget = widget.template ? WidgetTemplate : WidgetRender;
            if(widget.super == Vue){
                this.components[type] = widget.extend(WidgetComponent);
                return;
            }
            this.components[type] = {
                ...widget,
                extends: parentWidget
            }
        },
        props: {
            isDraggable: { // 
                type: Boolean,
                default: true
            },
            isResizable: { // 
                type: Boolean,
                default: true
            },
            rowHeight: { // 
                type: Number,
                default: 150
            },
            maxRows: { // 
                type: Number,
                default: Infinity
            },
            margin: {
                type: Array,
                default: () => [10, 10]
            }, // 
            verticalCompact: { // 
                type: Boolean,
                default: true
            },
            useCssTransforms: { // css transforms
                type: Boolean,
                default: true
            },
            colNum: { // 
                type: Number,
                default: 12
            },
            backgroundColor: { // 
                type: String,
                default: 'rgba(200,200,200,1)'
            },
            gridItemClass: { // class
                type: String,
                default: ''
            },
            closeHandlerClass: { // class
                type: String,
                default: ''
            },
            resizeHandlerClass: { // class
                type: String,
                default: 'alt-g-i-r-h-default-style'
            },
            placeholderClass: { //  placeholder class
                type: String,
                default: ''
            },
            isShowOriginCloseBtn: {
                type: Boolean,
                default: true
            }
        },
        data () {
            return {
                layout: [], // 
                defVal: {
                    minH: 1, // 
                    minW: 1, // 
                    maxH: Infinity, // 
                    maxW: Infinity, // 
                    isDraggable: true, // 
                    isResizable: true, // 
                    isShowOriginCloseBtn: true // 
                },
                containerHeight: 0, // 
                cols: [],
                cacheComputed: {},
                placeholder: null, // placeholder
                operator: 0, // 0 - 1 -  2 - 
                operatedItem: null, // 
                containerWidth: 0,
                boxWatchHandler: null,
                coors: null,
                timer: null,
                animation: null,
                animationHandler: null,
                eventHandler: {
                    mousedown: null,
                    mousemove: null,
                    mouseup: null
                }
            }
        },
        mounted: function () {
            this.initCols();
            this.boxWatchHandler = new watchBoxSize(this.$el, () => {
                this.initCols();
            })
            // this.initCols();
            // this.$nextTick(() => {
            //     this.erd = elementResizeDetectorMaker({
            //         strategy: "scroll"
            //     });
            //     this.erd.listenTo(this.$el, () => {
            //         this.initCols();
            //     });
            // })
            if(this.isDraggable || this.isResizable){
                this.bindEvents();
            }
            
        },
        destroyed(){
            this.boxWatchHandler.destroy();
            // this.erd.uninstall(this.$el);
            this.unbindEvents();
        },
        watch: {
            rowHeight(){
                this.reRenderStyle();
            },
            colNum(val){
                // console.log('change col number');
                if(this.coors){
                    this.coors.setMaxWidth = parseInt(val);
                }
                this.initCols();
            },
            cols(){
                // console.log('cols change');
                this.cacheComputed = {};
                this.reRenderStyle();
            },
            margin(){
                this.cacheComputed = {};
                this.reRenderStyle();
            },
            backgroundColor(){
                // this.reRenderStyle();
                this.forceReRenderStyle();
            }
        },
        computed: {
            containerStyle(){
                return {
                    height: this.containerHeight + 'px'
                }
            },
            operatorClass(){
                if(!this.operator) return '';
                if(this.operator === 1){
                    return 'alt-grid-container-operating alt-move';
                }else if(this.operator === 2){
                    return 'alt-grid-container-operating alt-resize';
                }
                return '';
            }
        },
        methods: {
            bindEvents(){
                this.eventHandler.mousedown = this.mousedown.bind(this);
                this.eventHandler.mousemove = this.mousemove.bind(this);
                this.eventHandler.mouseup = this.mouseup.bind(this);
                this.$el.addEventListener('mousedown', this.eventHandler.mousedown);
                document.addEventListener('mousemove', this.eventHandler.mousemove);
                document.addEventListener('mouseup', this.eventHandler.mouseup)
            },
            unbindEvents(){
                this.$el.removeEventListener('mousedown', this.eventHandler.mousedown);
                document.removeEventListener('mousemove', this.eventHandler.mousemove);
                document.removeEventListener('mouseup', this.eventHandler.mouseup);
            },
            canDragClass(isDraggable){
                return getFirstSetValue(isDraggable, this.isDraggable, true) ? 'can-drag' : '';
            },
            forceReRenderStyle(){
                if(this.timer) clearTimeout(this.timer);
                this.timer = setTimeout(() => {
                    this.layout.forEach((item) => {
                        let style = this.getCardStyle(item);
                        this.$set(item, 'style', style);
                        // item.style = style;
                    })
                }, 10);
            },
            reRenderStyle(ignoreId){
                if(this.timer) clearTimeout(this.timer);
                this.timer = setTimeout(() => {
                    this.containerHeight = 0;
                    this.layout.forEach((item, index) => {
                        if(item._id === ignoreId) return;
                        let card = this.$refs.cards[index];
                        let oldStyle = {
                            style: card.style,
                            w: card.style.width,
                            h: card.style.height,
                            transform: card.style.transform
                        };
                        if(oldStyle.transform){
                            oldStyle.transform = oldStyle.transform.replace(/\s/g, '');
                        }

                        let styleRaw = this.getCardStyle(item, true);

                        this.$set(item, 'style', styleRaw.style);
                        // item.style = style;
                        let status = this.getCardRectChangeStatus(oldStyle, styleRaw, ['w', 'h', 'transform']);
                        if(status === 'none') return;
                        this.dispatchEvent(index. status, {
                            w: item.w,
                            h: item.h,
                            x: item.x,
                            y: item.y
                        })

                        // console.log('create Style:', styleRaw, oldStyle, index);
                    })
                }, 10);
            },
            getCardRectChangeStatus(arg1, arg2, range){
                let keys = range || Object.keys(arg1);
                for(let i = 0, l = keys.length; i < l; i++){
                    let key = keys[i];
                    if(arg1[key] === arg2[key]){
                        if(key === 'w' || key === 'h'){
                            return 'move';
                        }
                        if(key === 'transform'){
                            return 'resize';
                        }
                    }
                }
                return 'none';
            },
            dispatchEvent(dragId, type, pos){
                this.$nextTick(() => {
                    this.$nextTick(() => {
                        this.$refs[dragId] && this.$refs[dragId][0].$emit(type, pos);
                    })
                })
                
            },
            getFirstSetValue(){
                return getFirstSetValue(...arguments);
            },
            getPropsForInject(index, item){
                return {
                    id: item._id,
                    card: item,
                    close: this.closeWidget.bind(this, item._id)
                }
            },
            // 
            initCols(){
                // // // console.log('init cols');
                let containerWidth = this.$el.clientWidth;
                if(this.colNum === this.cols.length &&
                    this.containerWidth && 
                    this.containerWidth === containerWidth){
                    return;
                }
                this.containerWidth = containerWidth;
                let colNum = this.colNum;
                let cols = [];
                // let containerWidth = this.$el.clientWidth;
                let remainder = containerWidth % colNum; // 
                let quotient = Math.floor(containerWidth / colNum); // 
                for(let i = 0; i < colNum; i++){
                    if(remainder){
                        cols[i] = quotient + 1;
                        remainder--;
                    }else{
                        cols[i] = quotient;
                    }
                }
                this.cols = cols;
            },
            // layout
            setLayout(layout){
                // this.layout = deepCopy(layout);
                // this.layout = layout;
                // // console.log(deepCopy)
                if(!this.coors){
                    this.coors = new Coordinate({
                        maxWidth: this.colNum
                    });
                }
                this.coors.clear();
                this.coors.batchAddItem(layout);
                
                let layoutOverCalc = this.coors.getAllItems();

                layoutOverCalc.forEach((item) => {
                    let style = this.getCardStyle(item);
                    this.$set(item, 'style', style);
                    // item.style = style;
                });

                this.layout = layoutOverCalc;

                this.$altStore.commit('log', {
                    type: 'setLayout'
                })

                this.$altStore.commit('addHistory', {
                    type: 'setLayout',
                    value: JSON.parse(JSON.stringify(this.layout))
                })

                if(/_env=altdev/.test(window.location.search)){
                    coorTest(this, this.layout);
                }
            },
            // 
            setContainerHeight(y, h){
                let containerHeight = this.containerHeight;
                let height = y + h;
                if(height > containerHeight){
                    this.containerHeight = height;
                }
            },
            // 
            getCardStyle(item, raw){
                if(!item) return {};
                let x = this.computeColsWidth(0, item.x);
                let w = this.getCardWidth(item.x, item.x + item.w);
                let y = item.y * this.rowHeight;
                let h = item.h * this.rowHeight - this.margin[1];
                this.setContainerHeight(y, h);
                let transform = `transform:translate3d(${x}px,${y}px,0);`;
                let style = `${transform}width:${w}px;height:${h}px;background-color:${this.backgroundColor};`;
                if(raw){
                    return { style, x, y, w, h, transform };
                }
                return style;
                // return {
                //     transform: `translate(${x}px,${y}px)`,
                //     width: w + 'px',
                //     height: h + 'px'
                // }
            },
            getCardStyleForRealTime(item){
                if(!item) return;
                let w = item.w;
                let x = item.x;
                if(x < 0){
                    x = 0;
                }else if((x + w) > this.containerWidth){
                    x = this.containerWidth - w;
                }
                let y = item.y;
                if(y < 0){
                    y = 0;
                }
                let transform = `transform:translate3d(${x}px,${y}px,0);`;
                let style = `${transform}width:${w}px;height:${item.h}px;background-color:${this.backgroundColor};z-index:1;`;
                return style;
            },
            getCardStyleForPlaceholder(item){
                if(!item) return {};
                let x = this.computeColsWidth(0, item.x);
                let w = this.getCardWidth(item.x, item.x + item.w);
                let y = item.y * this.rowHeight;
                let h = item.h * this.rowHeight - this.margin[1];
                this.setContainerHeight(y, h);
                let transform = `transform:translate3d(${x}px,${y}px,0);`;
                let style = `${transform}width:${w}px;height:${h}px;`;
                return style;
            },
            // 
            getCardWidth(start, end){
                let width = this.computeColsWidth(start, end);
                if(end !== (this.cols.length)){
                    width -= this.margin[0];
                }
                return width;
            },
            // 
            computeColsWidth(start, end){
                let key = start + ';' + end;
                if(this.cacheComputed[key]) return this.cacheComputed[key];
                let cols = this.cols;
                let width = 0;
                for(let i = start; i < end; i++){
                    width += cols[i];
                }
                this.cacheComputed[key] = width;
                return width;
            },
            computeRowsHeight(start, end){
                return (end - start) * this.rowHeight;
            },
            mousedown(evt){
                this.mousedownTimeStamp = new Date().getTime();
                let srcElement = evt.srcElement;
                let target = evt.target;
                let targetCard = findParentThoughEvtPath(evt.path, 'alt-grid-item', 'alt-grid-container');
                if(!targetCard) return;
                let dragId = this.getDragId(targetCard);
                let node = this.getNodeByDragId(dragId);
                if(hasClass(target, this.resizeHandlerClass)){
                    if(!getFirstSetValue(
                        node.isResizable, 
                        this.isResizable, 
                        this.defVal.isResizable)){
                        return;
                    }
                    this.operator = 2; // resize
                }
                if(targetCard && !this.operator){
                    if(!getFirstSetValue(
                        node.isDraggable, 
                        this.isDraggable, 
                        this.defVal.isDraggable)){
                        return;
                    }
                    this.operator = 1; // 
                }
                if(!targetCard && !this.operator) return;
                // if(!hasClass(target, 'alt-grid-item')) return;
                
                let targetCardStyle = targetCard.style;
                let translate = targetCardStyle.transform.match(/\(([-.\d]*)px, ([-.\d]*)px/);
                this.operatedItem = {
                    srcElement: srcElement,
                    el: targetCard,
                    node: node,
                    dragId: dragId,
                    linkEmit: this.$refs[dragId] ? this.$refs[dragId][0].$emit : function(){},
                    startX: evt.clientX,
                    startY: evt.clientY,
                    cacheStyle: {
                        x: parseInt(translate[1]),
                        y: parseInt(translate[2]),
                        w: parseInt(targetCardStyle.width.match(/\d+/)[0]),
                        h: parseInt(targetCardStyle.height.match(/\d+/)[0]),
                    }
                }

                this.placeholder = {
                    _id: '__placeHolder__',
                    x: node.x,
                    y: node.y,
                    w: node.w,
                    h: node.h
                };
                this.coors.removeItem(node);
                this.coors.addItem(this.placeholder);
                // console.log('down', evt, this.operatedItem);
                // if(hasClass(target, this.resizeHandlerClass)){
                //     this.operator = 2;
                // } else {
                //     this.operator = 1;
                // }
            },
            mousemove(evt){
                if(!this.operator) return;
                // console.log('mouse move');
                let ex = evt.clientX;
                let ey = evt.clientY;
                let sx = this.operatedItem.startX;
                let sy = this.operatedItem.startY;
                if(this.operator === 1){
                    this.dragMove(this.operatedItem, sx, sy, ex, ey);
                } else if(this.operator === 2){
                    this.resizeMove(this.operatedItem, sx, sy, ex, ey);
                }
            },
            mouseup(){
                let time = new Date().getTime();
                if(time - this.mousedownTimeStamp < 10){
                    this.operatedItem && this.operatedItem.srcElement && this.operatedItem.srcElement.click();
                }
                let item = this.operatedItem;
                if(item){
                    this.applyChange();
                    
                    // this.$set(item.node, 'style', this.getCardStyle(item.node));
                    item.node.style = this.getCardStyle(item.node);

                    this.coors.removeItem(this.placeholder);
                    this.coors.addItem(this.operatedItem.node);
                }

                this.clearDragEnv();
            },
            applyChange(){
                let x = this.placeholder.x;
                let y = this.placeholder.y;
                let w = this.placeholder.w;
                let h = this.placeholder.h;
                let dragId = this.operatedItem.dragId;
                let node = this.operatedItem.node;
                if(this.operator === 1){
                    if(node.x === x && node.y === y) return;
                    node.x = x;
                    node.y = y;
                    this.dispatchEvent(dragId, 'moved', {
                        x: x,
                        y: y,
                        w: node.w,
                        h: node.h
                    })
                }else if(this.operator === 2){
                    if(node.w === w && node.h === h)  return;
                    node.w = this.placeholder.w;
                    node.h = this.placeholder.h;
                    this.dispatchEvent(dragId, 'resized', {
                        x: node.x,
                        y: node.y,
                        w: w,
                        h: h
                    })
                }
            },
            clearDragEnv(){
                this.operator = 0;
                this.operatedItem = null;
                this.placeholder = null;
            },
            getNodeByDragId(dragId){
                let index = getIndexOfArrayByAttr(this.layout, dragId, '_id');
                if(index === -1) return null;
                return this.layout[index];
            },
            getDragId(target){
                return target.getAttribute('dg-id');
            },
            dragMove(item, sx, sy, ex, ey){
                // console.log('drag move');
                let node = this.placeholder;
                let cacheStyle = item.cacheStyle;
                let dx = ex - sx;
                let dy = ey - sy;
                let startCol = dx > 0 ? item.node.x + item.node.w : item.node.x;
                let stepX = this.getMoveCols(dx, startCol);
                let stepY = this.getMoveRows(dy, item.node.y);
                // console.log('calc over step');
                let targetX = item.node.x + stepX;
                let targetY = item.node.y + stepY;

                let moveUpRows = this.coors.getMoveUpRowsExceptId({
                    x: targetX,
                    y: targetY,
                    w: item.node.w,
                    h: item.node.h
                }, '__placeHolder__');

                targetY -= moveUpRows;

                this.coors.moveItemTo(node, {
                    x: targetX,
                    y: targetY
                })
                node.x = targetX;
                node.y = targetY;
                
                
                let x = cacheStyle.x + dx;
                let y = cacheStyle.y + dy;
                item.node.style = this.getCardStyleForRealTime({
                    x: x,
                    y: y,
                    w: cacheStyle.w,
                    h: cacheStyle.h
                })
                this.reRenderStyle(item.dragId);
                this.$altStore.commit('log', {
                    type: 'move'
                })
            },
            resizeMove(item, sx, sy, ex, ey){
                let node = this.placeholder;
                let cacheStyle = item.cacheStyle;
                let dx = ex - sx;
                let dy = ey - sy;
                let stepX = this.getMoveCols(dx, item.node.x + item.node.w);
                let stepY = this.getMoveRows(dy, item.node.y + item.node.h);
                let size = this.getItemLegalSize(item.node, {
                    w: item.node.w + stepX,
                    h: item.node.h + stepY
                })
                // console.log('resize', size.w, size.h)
                this.coors.resizeItem(node, {
                    w: size.w,
                    h: size.h
                })
                node.w = size.w;
                node.h = size.h;
                let w = cacheStyle.w + dx;
                let h = cacheStyle.h + dy;
                if(cacheStyle.x + w > this.containerWidth){
                    w = this.containerWidth - cacheStyle.x;
                }
                item.node.style = this.getCardStyleForRealTime({
                    x: cacheStyle.x,
                    y: cacheStyle.y,
                    w: w,
                    h: h
                })
                this.reRenderStyle(item.dragId);
                this.$altStore.commit('log', {
                    type: 'resize'
                })
            },
            getMoveCols(dx, startCol){
                if(startCol <= 0 && dx < 0) return 0;
                let flag = dx < 0 ? '-' : '+';
                let absDx = Math.abs(dx);
                if(absDx < 15) return 0;
                let i = 0;
                let c = startCol;
                while(absDx > 0){
                    if(flag === '-'){
                        c--;
                        absDx -= (this.cols[c] || 0);
                        i++;
                        if(c <= 0) break;
                    }else{
                        c++;
                        absDx -= (this.cols[c] || 0);
                        i++;
                        if(c >= this.cols.length) break;
                    }
                    
                }
                return parseInt(flag + i);
            },
            getMoveRows(dy, startRow){
                if(startRow <= 0 && dy < 0) return 0;
                let flag = dy < 0 ? '-' : '+';
                let absDy = Math.abs(dy);
                if(absDy < this.rowHeight/2) return 0;
                let i = 0;
                let row = startRow;
                while(absDy > 0){
                    if(flag === '-'){
                        absDy -= this.rowHeight;
                        i++;
                        row--;
                        if(row <= 0) break;
                    } else {
                        absDy -= this.rowHeight;
                        i++;
                        row++;
                    }
                }
                return parseInt(flag + i);
            },
            addItem(item){
                if(this.coors){
                    let distributeItem = this.coors.addItem(item);
                    let style = this.getCardStyle(distributeItem);
                    this.$set(distributeItem, 'style', style);
                    this.layout.push(distributeItem);
                    this.$altStore.commit('addHistory', {
                        type: 'addItem',
                        value: JSON.parse(JSON.stringify(this.layout))
                    });
                    return distributeItem._id;
                }
            },
            deleteItem(id){
                return this.closeWidget(id);
            },
            getAllItems(){
                return this.layout;
            },
            getItemLegalSize(item, size){
                
                let minH = getFirstSetValue(item.minH, this.defVal.minH);
                let minW = getFirstSetValue(item.minW, this.defVal.minW);
                let maxH = getFirstSetValue(item.maxH, this.defVal.maxH);
                let maxW = getFirstSetValue(item.maxW, this.defVal.maxW);
                let h = size.h;
                let w = size.w;
                if(size.h <= minH){
                    h = minH;
                }
                if(size.h >= maxH){
                    h = maxH;
                }
                if(size.w <= minW){
                    w = minW;
                }
                if(size.w >= maxW){
                    w = maxW;
                }
                return {
                    h: h,
                    w: w
                }
            },
            closeWidget(_id){
                let index = getIndexOfArrayByAttr(this.layout, _id, '_id');
                if(index === -1) return false;
                let item = this.layout[index];
                this.coors.removeItem(item);
                this.coors.moveAllItemUp();
                this.layout.splice(index, 1);
                // this.reRenderCount++;
                this.clearDragEnv();
                this.$altStore.commit('addHistory', {
                    type: 'deleteItem',
                    value: JSON.parse(JSON.stringify(this.layout))
                });
            },
            go(num){
                let historyItem = this.$altStore.state.historyStack.go(num);
                let layoutCopy = historyItem.value;
                if(!layoutCopy.length) return;
                
                this.layout = layoutCopy;
                this.coors.clear();
                this.coors.batchAddItem(this.layout, true);

                this.$altStore.commit('log', {
                    type: 'go',
                    value: num
                })
            }
        }
    }
</script>

<style>
.alt-grid-container{
    position: relative;
    /* border: 1px solid red; */
    /* box-sizing: border-box; */
}
.alt-grid-container .alt-grid-item{
    position: absolute;
    background: gray;
    /* cursor: move; */
}
.alt-grid-container .alt-grid-item.can-drag{
